<template>
    <div>
        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="payOrder">
              <table class="table">
                <thead>
                  <th></th>
                  <th></th>
                  <th width="100"></th>
                </thead>
                <tbody>
                  <tr v-for="item in order.products" :key="item.id">
                    <td class="align-middle">{{ item.product.title }}</td>
                    <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="align-middle text-right">{{ item.final_total | currency }}</td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td colspan="2" class="text-right"></td>
                    <td class="text-right">{{ order.total | currency }}</td>
                  </tr>
                </tfoot>
              </table>
          
              <table class="table">
                <tbody>
                  <tr>
                    <th width="100">Email</th>
                    <td>{{ order.user.email }}</td>
                  </tr>
                  <tr>
                    <th></th>
                    <td>{{ order.user.name }}</td>
                  </tr>
                  <tr>
                    <th></th>
                    <td>{{ order.user.tel }}</td>
                  </tr>
                  <tr>
                    <th></th>
                    <td>{{ order.user.address }}</td>
                  </tr>
                  <tr>
                    <th></th>
                    <td>
                      <span v-if="!order.is_paid"></span>
                      <span v-else class="text-success"></span>
                    </td>
                  </tr>
                </tbody>
              </table>
              <div class="text-right" v-if="order.is_paid === false">
                <button class="btn btn-danger"></button>
              </div>
            </form>
          </div>
    </div>
</template>
<script>
export default{
    data(){
        return{
            orderId:'',
            order:{
                user:{},
            },
        }
    },
    methods: {
        getOrder(){
            const vm = this;
            const api=`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`; //orderIdcreatedid
                vm.isLoading = true;
                this.$http.get(api).then((response) => {
                console.log(response);
                vm.order = response.data.order;
                vm.isLoading = false;
                });
        },
        payOrder(){
            const vm = this;
            const api=`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`; //orderIdcreatedid
                vm.isLoading = true;
                this.$http.post(api).then((response) => {
                console.log(response);
                if(response.data.success){
                    vm.getOrder();
                }
                vm.isLoading = false;
                });
        },
    },
    created(){
        this.orderId = this.$route.params.orderId;//orderIdrouterindex.js(!)
        console.log(this.orderId); //id
        this.getOrder();
        
    },
}
</script>


